.title {
  font-size: x-large;
  border: solid 4px black;
  background-color: lightcyan;
  padding: 70px 0;
  text-align: center;
}

.description {
  border: solid 4px black;
  background-color: lightcyan;
  padding: 70px 0;
  text-align: center;
}

.grid div {
  float: left;
}

.grid::after {
  content: "";
  display: table;
  clear: both;
}

.grid .col-1 {
  width: 8.3333333333%;
  background-color: blue;
}

.grid .col-2 {
  width: getWidth(6);
  background-color: blue;
}

.grid .col-3 {
  width: 25%;
  background-color: blue;
}

.grid .col-4 {
  width: 33.3333333333%;
  background-color: blue;
}

.grid .col-6 {
  width: 50%;
  background-color: blue;
}

.grid .col-8 {
  width: 76.9230769231%;
  background-color: blue;
}

.grid .col-12 {
  width: 100%;
  background-color: blue;
}

